@charset "utf-8";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
body {
    color: #333;
}
button{
    background: inherit ; border:none; box-shadow:none; border-radius:0; padding:0; overflow:visible; 
}
.header{
    margin: 30px 0;
}

.header .inner{
    display: flex;
    justify-content:center;
    align-items: center;
    gap: 20px;

}

.header .inner img{
    width: 40px;
    height: 40px;
    object-fit: cover;
    display: block;
    rotate: 20deg;
    order: 2;
}

.header .inner span{
    order: 3;
}

.header h1{
    font-family: 'CookieRun-Regular';
    order: 1;
}

.check{
    max-width: 640px;
    width: calc(100% - 40px);
    margin: 0 auto;
    position: relative;
}

.check form {
    margin-bottom: 50px;
}

.check form input[type="text"]{
    width: 100%;
    height: 50px;
    border: 1px solid #eee;
    border-radius: 10px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    padding: 15px;
}
/* 
.check form input::placeholder{
    padding: 15px;
} */

.check .addbtn{
    position: absolute;
    top: 10px;
    right: 10px;
}

.check .addbtn img{
    width: 30px;
    height: 30px;
    display: block;
}

input:focus{
    outline: none;
}


.check .check_list ul{
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    padding: 20px 50px;
}

.check .check_list li{
    margin: 20px 0;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
}